// components/GlobeStates.tsx
import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import Globe from 'react-globe.gl';
import * as topojson from 'topojson-client';
import type { FeatureCollection, Feature } from 'geojson';

import {
  JurisdictionId,
  jurisdictionById,
} from '../lib/jurisdictions';

export interface RegionSelection {
  id: string;
  name: string;
}

interface GlobeStatesProps {
  jurisdiction: JurisdictionId;
  onRegionSelect?: (region: RegionSelection) => void;
  onViewChange?: (altitude: number) => void;
}

// Helper to safely pull a property from a feature
function getProp(feature: Feature, key: string): string | null {
  const value = (feature.properties as any)?.[key];
  if (value == null) return null;
  return String(value);
}

// Fallback: use "name" property if present
function getName(feature: Feature): string {
  const props = (feature.properties || {}) as any;
  return String(props.name ?? props.NAME_1 ?? props.NAME ?? 'Unknown region');
}

const BASE_ALTITUDE = 0.008;
const SELECTED_EXTRA = 0.022;
const HOVER_EXTRA = 0.010;

const GlobeStates: React.FC<GlobeStatesProps> = ({
  jurisdiction,
  onRegionSelect,
  onViewChange,
}) => {
  const globeRef = useRef<any>(null);

  const [features, setFeatures] = useState<Feature[]>([]);
  const [hovered, setHovered] = useState<Feature | null>(null);
  const [selectedId, setSelectedId] = useState<string | null>(null);

  const spec = jurisdictionById[jurisdiction];

  // Load ADM1 polygons for active jurisdiction
  useEffect(() => {
    let cancelled = false;

    async function load() {
      if (!spec.topoJsonPath) {
        setFeatures([]);
        return;
      }

      try {
        const res = await fetch(spec.topoJsonPath);
        if (!res.ok) {
          console.warn(
            `Failed to load topojson for ${spec.id}: ${res.status}`,
          );
          if (!cancelled) setFeatures([]);
          return;
        }

        const topo = await res.json();
        const objects = (topo as any).objects;
        const objectName =
          spec.topoObjectName || Object.keys(objects)[0];
        const fc = topojson.feature(
          topo as any,
          objects[objectName],
        ) as FeatureCollection;

        if (!cancelled) {
          setFeatures(fc.features);
        }
      } catch (err) {
        console.error('Error loading TopoJSON', err);
        if (!cancelled) setFeatures([]);
      }
    }

    setSelectedId(null);
    setHovered(null);
    load();

    return () => {
      cancelled = true;
    };
  }, [spec]);

  // Focus camera when jurisdiction changes
  useEffect(() => {
    const globe = globeRef.current;
    if (!globe) return;

    const { lat, lng, altitude } = spec.focus;
    globe.pointOfView({ lat, lng, altitude }, 1000);
  }, [spec]);

  // Report altitude back up on camera moves
  useEffect(() => {
    if (!globeRef.current || !onViewChange) return;

    const globe = globeRef.current;
    const controls = globe.controls?.();
    if (!controls) return;

    const handleChange = () => {
      const pov = globe.pointOfView();
      if (pov && typeof pov.altitude === 'number') {
        onViewChange(pov.altitude);
      }
    };

    controls.addEventListener('change', handleChange);
    return () => {
      controls.removeEventListener('change', handleChange);
    };
  }, [onViewChange]);

  // Altitude / explosion logic
  const polygonAltitude = useCallback(
    (f: Feature) => {
      const idProp = spec.featureIdProp;
      const id = getProp(f, idProp);
      const isSelected = id && selectedId && id === selectedId;
      const isHovered = hovered === f;

      let extra = 0;
      if (isSelected) extra += SELECTED_EXTRA;
      if (isHovered) extra += HOVER_EXTRA;

      return BASE_ALTITUDE + extra;
    },
    [hovered, selectedId, spec.featureIdProp],
  );

  const polygonCapColor = useCallback(
    (f: Feature) => {
      const id = getProp(f, spec.featureIdProp);
      const isSelected = id && selectedId && id === selectedId;
      const isHovered = hovered === f;

      if (isSelected) return '#22c55e';
      if (isHovered) return '#4ade80';
      return 'rgba(15,23,42,0.98)'; // slate-900-ish
    },
    [hovered, selectedId, spec.featureIdProp],
  );

  const polygonStrokeColor = useCallback(
    (f: Feature) => {
      const id = getProp(f, spec.featureIdProp);
      const isSelected = id && selectedId && id === selectedId;
      if (isSelected) return 'rgba(34,197,94,0.9)';
      return 'rgba(30,64,175,0.4)'; // subtle blue
    },
    [selectedId, spec.featureIdProp],
  );

  const polygonsData = useMemo(() => features, [features]);

  const handleClick = useCallback(
    (f: Feature) => {
      const id =
        getProp(f, spec.featureIdProp) ?? getName(f);

      setSelectedId(id);

      if (onRegionSelect) {
        onRegionSelect({
          id,
          name: getName(f),
        });
      }

      // Optional: gently nudge camera toward clicked region
      if (globeRef.current) {
        const centroid = (f as any).properties?.centroid;
        if (
          centroid &&
          typeof centroid[1] === 'number' &&
          typeof centroid[0] === 'number'
        ) {
          globeRef.current.pointOfView(
            {
              lat: centroid[1],
              lng: centroid[0],
              altitude: spec.focus.altitude,
            },
            700,
          );
        }
      }
    },
    [onRegionSelect, spec.featureIdProp, spec.focus.altitude],
  );

  return (
    <div className="h-full w-full">
      <Globe
        ref={globeRef}
        backgroundColor="rgba(0,0,0,1)"
        animateIn
        // Base sphere texture – simple + fast
        globeImageUrl="/textures/earth-base.jpg"
        // Only draw polygons for the active jurisdiction
        polygonsData={polygonsData}
        polygonAltitude={polygonAltitude}
        polygonCapColor={polygonCapColor}
        polygonSideColor={() => 'rgba(15,23,42,1)'}
        polygonStrokeColor={polygonStrokeColor}
        polygonsTransitionDuration={260}
        polygonLabel={(f: Feature) => getName(f)}
        onPolygonHover={(f: Feature | null) => setHovered(f)}
        onPolygonClick={(f: Feature) => handleClick(f)}
      />
    </div>
  );
};

export default GlobeStates;
