// lib/jurisdictions.ts
export type JurisdictionId =
  | 'unitedStates'
  | 'canada'
  | 'mexico'
  | 'netherlands'
  | 'uruguay'
  | 'germany'
  | 'southAfrica'
  | 'luxembourg'
  | 'malta'
  | 'georgia';

export interface JurisdictionSpec {
  id: JurisdictionId;
  label: string;          // UI label in the tabs
  code: string;           // ISO-ish code, used for API namespace etc.
  description: string;    // Short tagline for the card
  focus: { lat: number; lng: number; altitude: number };
  topoJsonPath?: string;      // Where we load ADM1 polygons from
  topoObjectName?: string;    // Name of TopoJSON object (defaults to first)
  featureIdProp: string;      // Property used as stable id (e.g. "code" or "postal")
}

